/* QuickLinks Container */
.quicklinks-container {
  min-height: 100vh;
  min-width: 70vw;
  /* background: linear-gradient(135deg, #0f172a 0%, #7c3aed 50%, #0f172a 100%); */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  margin-left: 0%;
}

/* Main Card */
.quicklinks-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(16px);
  border-radius: 1rem;
  padding: 2rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.2);
  max-width: 600px;
  width: 100%;
  min-width: 120%;
  margin-top: -10%;
  margin-left: 40%;
}

/* Header */
.quicklinks-header {
  text-align: center;
  margin-bottom: 2rem;
}

.quicklinks-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: white;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.title-icon {
  width: 2rem;
  height: 2rem;
}

.quicklinks-subtitle {
  color: #d1d5db;
  font-size: 1rem;
  margin: 0;
}

/* Grid Layout */
.quicklinks-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .quicklinks-grid {
    grid-template-columns: repeat(8, 1fr);
  }
}

/* QuickLink Items */
.quicklink-item {
  border-radius: 0.75rem;
  padding: 1.5rem;
  cursor: pointer;
  transform: scale(1);
  transition: all 0.3s ease;
  color: white;
  position: relative;
  overflow: hidden;
}

.quicklink-item:hover {
  transform: scale(1.1);
  box-shadow: 0 20px 40px -12px rgba(0, 0, 0, 0.4);
}

.quicklink-item:active {
  transform: scale(0.95);
}

/* Color Variants */
.quicklink-item.youtube {
  background: linear-gradient(135deg, #ef4444, #dc2626);
}

.quicklink-item.youtube:hover {
  background: linear-gradient(135deg, #dc2626, #b91c1c);
}

.quicklink-item.udemy {
  background: linear-gradient(135deg, #8b5cf6, #7c3aed);
}

.quicklink-item.udemy:hover {
  background: linear-gradient(135deg, #7c3aed, #6d28d9);
}

.quicklink-item.chatgpt {
  background: linear-gradient(135deg, #10b981, #059669);
}

.quicklink-item.chatgpt:hover {
  background: linear-gradient(135deg, #059669, #047857);
}

.quicklink-item.claude {
  background: linear-gradient(135deg, #f97316, #ea580c);
}

.quicklink-item.claude:hover {
  background: linear-gradient(135deg, #ea580c, #dc2626);
}

/* Content Layout */
.quicklink-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
}

.quicklink-icon-wrapper {
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  transition: background-color 0.3s ease;
}

.quicklink-item:hover .quicklink-icon-wrapper {
  background: rgba(255, 255, 255, 0.3);
}

.quicklink-icon {
  width: 2rem;
  height: 2rem;
}

.quicklink-name {
  font-weight: 600;
  font-size: 0.875rem;
  text-align: center;
}

/* Footer */
.quicklinks-footer {
  text-align: center;
}

.quicklinks-help-text {
  color: #9ca3af;
  font-size: 0.875rem;
  margin: 0;
}

/* Responsive Design */
@media (max-width: 640px) {
  .quicklinks-container {
    padding: 1rem;
  }
  
  .quicklinks-card {
    padding: 1.5rem;
  }
  
  .quicklinks-title {
    font-size: 2rem;
  }
  
  .quicklinks-grid {
    gap: 1rem;
  }
  
  .quicklink-item {
    padding: 1rem;
  }
  
  .quicklink-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
  
  .title-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
}

/* Animation Effects */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.quicklinks-card {
  animation: fadeIn 0.6s ease-out;
}

.quicklink-item {
  animation: fadeIn 0.6s ease-out;
}

.quicklink-item:nth-child(1) { animation-delay: 0.1s; }
.quicklink-item:nth-child(2) { animation-delay: 0.2s; }
.quicklink-item:nth-child(3) { animation-delay: 0.3s; }
.quicklink-item:nth-child(4) { animation-delay: 0.4s; }

/* Accessibility */
.quicklink-item:focus {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}

.quicklink-item:focus-visible {
  outline: 2px solid #3b82f6;
  outline-offset: 2px;
}